import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';
import { Camera, CameraResultType , CameraSource } from '@capacitor/camera';


@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.page.html',
  styleUrls: ['./add-customer.page.scss'],
})
export class AddCustomerPage implements OnInit {

  public Insert_Customer : any ={name:'',father_name:'', cnic:'',  cnic_img:'' , mobile_no: '' , address:'',query:'' }

  constructor(public apiCall:ApicallService, private router: Router , public global: GlobalService) { }

  ngOnInit() {
  }

    // Insert Plot_Data
    async InsertCustomer(){
      console.log(this.Insert_Customer);
      await this.apiCall.api_insert_customer(this.Insert_Customer);
    this.Insert_Customer = {name:'',father_name:'', cnic:'',  cnic_img:'' , mobile_no: '' , address:'' ,query:''}
    document.getElementById('cameraImage').setAttribute('src', './../../../assets/placholder_img.png');
    }


    async takePictureBase64(){
      const image = await Camera.getPhoto({
        quality:90,
        resultType: CameraResultType.Base64,
        source: CameraSource.Prompt,
        allowEditing: true
      });
      document.getElementById('cameraImage').setAttribute('src', `data:image/${image.format};base64,`+image.base64String );
      console.log(image.base64String);
      this.Insert_Customer.cnic_img = image.base64String;
    }

    Property(){
      this.router.navigate(['property'])
    }
    Add_Customer(){
      this.router.navigate(['add-customer'])
    }
    Customer(){
      this.router.navigate(['customers'])
    }
    Home(){
      this.router.navigate(['home'])
    }

}
